import React from 'react'
import { Box } from 'grommet'
import { Cloudlinux } from 'grommet-icons'
import { branch, renderComponent } from 'recompose'

export const DumbLoading = props => (
  <Box fill justify='center' align='center' pad='large'>
    <Cloudlinux id="loading-box" color='neutral-3' size='xlarge' />
  </Box>
)

const renderWhileLoading = (component, propName = 'data') =>
  branch(
    props => props[propName] && props[propName].networkStatus !== 7,
    renderComponent(component)
  )

export default renderWhileLoading(DumbLoading)