import React from 'react'
import { Box, Button, DropButton, Grid, Text } from 'grommet'
import Avatar from '../commons/Avatar'
import { Apps, Aid, Close, Currency, Waypoint } from 'grommet-icons'
import moment from 'moment'
import { withRouter } from 'react-router-dom'

const renderType = typename => {
  let color = 'status-critical'
  let icon = (<Aid color='white'/>)
  switch (typename) {
    case 'Penjualan':
      icon = (<Currency color='white'/>)
      color = 'status-ok';
      break;
    case 'Penggunaan':
      icon = (<Waypoint color='white'/>)
      color = 'neutral-2';
      break;
    default:
      break;
  }
  return (
    <Box
      direction='row'
      gap='small'
      align='center'
    >
      <Box pad='small' round='full' background={color}>
        {icon}
      </Box>
      <Text size='small' weight='bold'>{typename}</Text> 
    </Box>
  )
}

const Mutations = withRouter(props => {
  return (
    <Box>
      {
        props.items.map(it => {
          return (
            <Box 
              pad='small'
              border={{
                side: 'bottom',
                size: 'small',
                color: 'light-4'
              }}
            >
              <Grid
                fill
                columns={[
                  '20%',
                  '20%',
                  'flex',
                  '10%',
                  '10%',
                ]}
                rows={[
                  'flex'
                ]}
                areas={[
                  { name: 'type', start:[0,0], end: [0,0] },
                  { name: 'user', start:[1,0], end: [1,0] },
                  { name: 'identity', start:[2,0], end: [2,0] },
                  { name: 'jumlah', start:[3,0], end: [3,0] },
                  { name: 'action', start:[4,0], end: [4,0] }
                ]}
              >
                <Box gridArea='type' justify='center'>
                  {renderType(it.__typename)}
                </Box>

                <Box gridArea='user' justify='center' direction='row' align='center' gap='small'>
                  <Avatar avatar={it.addedBy.avatar} round='false' />
                  <Text 
                    size='small' 
                    weight='bold'
                  >
                    {it.addedBy.nama}
                  </Text>
                </Box>

                <Box gridArea='identity' justify='center'>
                  <Text size='small' weight='bold'>{it.nominal} IDR</Text>
                  <Box direction='row' gap='small'>
                    <Text size='small'>{moment(it.waktu).calendar()}</Text>
                    -
                    <Text size='small'>{moment(it.waktu).fromNow()}</Text>
                  </Box>
                </Box>

                <Box gridArea='jumlah' justify='center'>
                  <Text weight='bold'>
                    {it.jumlah}
                  </Text>
                </Box>

                <Box gridArea='action' justify='center'>
                  <DropButton
                    plain
                    icon={<Apps size='small'/>}
                    dropContent={
                      <Box pad='small' gap='small'>
                        <Box
                          style={{
                            cursor: 'pointer'
                          }}
                          onClick={() => {
                            props.history.push(`/app/detail/mutation/${it.id}`)
                          }}
                        >
                          <Text>Detail</Text>
                        </Box>
                        <Box>
                          <Text>Delete</Text>
                        </Box>
                      </Box>
                    }
                  ></DropButton>
                </Box>
              </Grid>
            </Box>
          )
        })
      }
    </Box>
  )
})

export default Mutations;