import React, { Component } from 'react'
import { Box, DropButton, Text, Select } from 'grommet'
import { Down } from 'grommet-icons'
import { GQLCustomerById } from '../../services'
import { compose, withState, mapProps } from 'recompose'
import moment from 'moment'
import Loading from '../commons/Loading'
import SesiRow from '../sesi/Row'
import SellItem from './SellItem'

const withId = mapProps(({ match }) => {
  return {
    id: parseInt(match.params.id)
  }
})

const extractItem = mapProps(props => {
  let { sessions, __typename, ...item } = props.data.userById
  if (item.kunjunganTerakhir !== 'Never') {
    item.kunjunganTerakhir = moment(item.kunjunganTerakhir).calendar()
  }
  return {
    ...props,
    item,
    typename: __typename,
    listSesi: sessions
  }
})

const enchance = compose(
  withId,
  withState('skip', 'setSkip', 0),
  withState('take', 'setTake', 30),
  GQLCustomerById,
  Loading,
  extractItem
)

class DetailCustomer extends Component {

  state = {
    mode: 'read'
  }

  renderAction = () => {
    return (
      <DropButton
        icon={<Down/>}
        dropContent={
          <Box pad='small' gap='small'>
            <Box
              onClick={e => {
                this.setState({
                  mode: 'sell-item'
                })
              }}
            >Jual Barang</Box>
            <Box>Delete</Box>
          </Box>
        }
      >
      </DropButton>
    )
  }

  renderSesiList = () => {
    return (
      <Box gap='small'>
        {this.props.listSesi.map((it, index) => (<SesiRow key={`sesi-id${it.id}`} item={it} /> ))}
      </Box>
    )
  }

  renderDescription = () => {
    const imageSize = '150px'
    return (
      <Box
        direction='row'
        gap='medium'
      >
        <Box
          style={{
            minWidth: imageSize,
            minHeight: imageSize
          }}
          background={`url(${this.props.item.avatar})`}
          border={{
            color: 'neutral-3',
            size: 'large'
          }}
        />

        <Box
          direction='column'
          justify='center'
        >
          <Box
            direction='column'
            justify='start'
            gap='small'
          >
            <Text size='large' weight='bold'>{this.props.item.nama}</Text>
            <Text weight='bold'>{this.props.item.username}</Text>
            <Text>Total Kunjungan: {this.props.item.totalKunjungan}</Text>
            <Text>Kunjungan Terakhir: {this.props.item.kunjunganTerakhir}</Text>
          </Box>
        </Box>
        
      </Box>
    )
  }
  
  render () {
    return (
      <Box gap='medium'>

        {/* Sell Item Dialog */}
        {
          (this.state.mode === 'sell-item')
          && (<SellItem 
                idUser={this.props.item.id} 
                done={() => this.setState({ mode: 'read' })}
              />)
        }

        <Box gap='small' direction='row' align='center'>
          <Text weight='bold' size='large'>
            Detail Customer
          </Text>
        
        {
          this.renderAction()
        }

        </Box>

        {this.renderDescription()}

        <Box gap='small'>
          <Box direction='row' align='center' gap='medium'>
            <Text>Jumlah data sesi yang ditampilkan</Text>
            <Select
              options={[30, 50, 100]}
              value={this.props.take}
              onChange={({ option }) => {
                this.props.setTake(option)
              }}
              style={{
                maxWidth: '300px'
              }}
            >
            </Select>
          </Box>
          {this.renderSesiList()}
        </Box>
      </Box>
    )
  }
}

export default enchance(DetailCustomer)