import React, { Component, Fragment } from 'react'
import { withRouter } from 'react-router-dom'
import { Box, Text, DropButton, Grid } from 'grommet'
import { Apps } from 'grommet-icons'
import moment from 'moment'
import TimeInfo from './TimeInfo'
import RowMenuBox from './RowMenuBox'
import Avatar from '../commons/Avatar'
import Rating from '../commons/Rating'
import LinkText from '../commons/LinkText'
import SesiType from './SesiType'

class Row extends Component {

  constructor (props) {
    super(props)
    let userAvatarSize = '42px'
    this.userAvatarStyle = {
      minWidth: userAvatarSize,
      minHeight: userAvatarSize,
      maxWidth: userAvatarSize,
      maxHeight: userAvatarSize
    }
  }

  userAvatarSize = '42px'

  render () {
    let { state } = this.props.item

    return (
      <Box 
        direction='row' 
        gap='small' 
        background='light-2'
      >
        <Box pad='xsmall' 
          justify='center'
          align='center'
          style={{
            minWidth: '100px'
          }}  
        >
          <SesiType state={state} pad='xsmall' size='small'/>
        </Box>

        <Box fill='horizontal' pad='xsmall'>
          <Grid
            fill
            columns={['40%', '40%', '20%']}
            rows={['flex']}
            areas={[
              { name:'info', start: [0,0], end: [0,0] },
              { name:'extra', start: [1,0], end: [1,0] },
              { name:'actions', start: [2,0], end: [2,0] }
            ]}
          >
            <Box gridArea='info' fill direction='row' align='center'>
              <Avatar avatar={this.props.item.user.avatar} />
              <Box justify='center' align='start' pad='xsmall' gap='small' fill>

                <Box 
                  direction='column'
                  justify='center'
                  pad={{ right: 'xsmall' }}
                >
                  <LinkText
                    size='small'
                    weight='bold'
                    text={this.props.item.user.nama}
                    onClick={event => {
                      this.props.history.push('/app/detail/customer/' + this.props.item.user.id)
                    }}
                  />
                  <Box direction='row' gap='small'>
                    <LinkText 
                      size='xsmall'
                      text={this.props.item.barbermen.nama} 
                      onClick={event => {
                        this.props.history.push('/app/detail/barbermen/' + this.props.item.barbermen.id)
                      }}
                    />
                    <LinkText 
                      size='xsmall'
                      text={this.props.item.paketJasa.nama} 
                      onClick={event => {
                        this.props.history.push('/app/detail/paketjasa/' + this.props.item.paketJasa.id)
                      }}
                    />
                  </Box>
                </Box>

              </Box>
            </Box>

            <Box gridArea='extra' align='center' gap='xsmall' justify='center'>
              { 
                (this.props.item.state !== 'DONE')
                ? (<TimeInfo
                    state={this.props.item.state}
                    time={{
                      scheduledStartTime: this.props.item.scheduledStartTime,
                      executionStartTime: this.props.item.executionStartTime,
                      executionEndTime: this.props.item.executionEndTime
                    }}
                  ></TimeInfo>)
                : (
                  <Fragment>
                    <Rating rating={this.props.item.rating} />
                    <Text size='xsmall'>Berakhir: {moment(this.props.item.executionEndTime).calendar()}</Text>
                  </Fragment>)
              }
            </Box>
            <Box gridArea='actions' align='center' gap='xsmall' justify='center'>
              <DropButton
                dropAlign='bottom'
                dropContent={<RowMenuBox state={this.props.item.state} id={this.props.item.id} />}
              >
                <Apps/>
              </DropButton>
            </Box>

          </Grid>
        </Box>
      
      </Box>
    )
  }
}

export default withRouter(Row)