import React, { Component, Fragment } from 'react'
import { Box, DropButton, Grid, Text } from 'grommet'
import { Down } from 'grommet-icons'
import Avatar from '../commons/Avatar'
import TimeInfo from '../sesi/TimeInfo'
import SesiType from '../sesi/SesiType'
import Rating from '../commons/Rating'
import RowMenuBox from '../sesi/RowMenuBox'
import { GQLSessionById } from '../../services'
import Loading from '../commons/Loading'
import { compose, mapProps } from 'recompose'
import { withRouter } from 'react-router-dom'
import moment from 'moment' 

const enchance = compose(
  withRouter,
  mapProps(({ match, ...rest }) => ({
    id: parseInt(match.params.id),
    ...rest
  })),
  GQLSessionById,
  Loading,
  mapProps(({ data: { sessionById }, ...rest }) => ({
    item: sessionById,
    ...rest
  }))
)

class DetailSesi extends Component {

  formatTime = (time) => (moment(time).format('DD/MM/YYYY h:mm:ss a'))

  renderInfo = ({ label, value }) => {
    return (
      <Box 
        direction='row' 
        justify='between' 
        align='center' 
        pad={{ vertical: 'small', horizontal: 'small' }}
        border={{
          side: 'bottom',
          size: 'xsmall',
          color: 'dark-2'
        }}
      >
        <Text size='small'>{label}</Text>
        <Text size='small'>{value}</Text>
      </Box>
    )
  }

  renderMember =  ({ grid, nama, avatar, onClick }) => {
    return (
      <Box
        gridArea={grid}
        background='light-2'
        justify="center" 
        align="center"
        gap="medium"
        pad={{
          vertical: 'small'
        }}
        onClick={onClick}
      >
        <Avatar size='200px' avatar={avatar}></Avatar>
        <Text size='large' weight="bold">{nama}</Text>
      </Box>
    )
  }

  render () {
    const { user, barbermen, paketJasa } = this.props.item
    return (
      <Box pad={{ vertical: 'small', bottom: 'large' }}>
        <Grid
          gap='small'
          columns={[
            '1/3',
            '1/3',
            '1/3'
          ]}
          rows={[
            'auto',
            'flex',
            'flex'
          ]}
          areas={[
            { name: 'header', start: [0,0], end: [2,0] },
            { name: 'main-desc', start: [0, 1], end: [0, 1]},
            { name: 'additional-desc', start: [1, 1], end: [1, 1]},

            { name: 'user', start: [0,2], end: [0,2]},
            { name: 'barbermen', start: [1,2], end: [1,2]},
            { name: 'paket-jasa', start: [2,2], end: [2,2]},
          ]}
        >
          <Box
            gridArea='header'
            direction='row'
            gap='medium'
            align='center'
          >
            <Text>Detail Sesi #ID</Text>
            <DropButton
              icon={<Down/>}
              dropContent={<RowMenuBox 
                    state={this.props.item.state} 
                    id={this.props.item.id} 
                    doneDelete={() => {
                      this.props.history.push('/app/sesi/all')
                    }}
                  />}
            ></DropButton>
          </Box>

          <Box gridArea='main-desc' justify='start'>
            <SesiType state={this.props.item.state} pad='small' size='medium'/>
            { 
              (this.props.item.state !== 'DONE')
              ? (<TimeInfo
                  state={this.props.item.state}
                  time={{
                    scheduledStartTime: this.props.item.scheduledStartTime,
                    executionStartTime: this.props.item.executionStartTime,
                    executionEndTime: this.props.item.executionEndTime
                  }}
                ></TimeInfo>)
              : (
                <Fragment>
                  <Rating 
                    rating={this.props.item.rating} 
                    size='large' pad='medium'
                    refetchQueries={['SessionById']}
                    id={this.props.item.id}
                  />
                  <Text size='medium' weight='bold'>Berakhir: {moment(this.props.item.executionEndTime).calendar()}</Text>
                </Fragment>)
            }
          </Box>

          <Box gridArea="additional-desc" pad={{ horizontal: 'small' }}>
            { 
              this.renderInfo({ 
                label: 'Jadwal Mulai', 
                value: this.formatTime(this.props.item.scheduledStartTime)
              }) 
            }
            { 
              this.renderInfo({ 
                label: 'Jadwal Berakhir', 
                value: this.formatTime(this.props.item.scheduledEndTime)
              }) 
            }
            { 
              this.renderInfo({ 
                label: 'Waktu Mulai', 
                value: this.formatTime(this.props.item.executionStartTime)
              }) 
            }
            { 
              this.renderInfo({ 
                label: 'Waktu Berakhir', 
                value: this.formatTime(this.props.item.executionEndTime)
              }) 
            }
            { 
              this.renderInfo({ 
                label: 'Total bayar', 
                value: this.props.item.paketJasa.harga
              }) 
            }
          </Box>

          {
            this.renderMember({ 
              nama: user.nama,
              avatar: user.avatar,
              grid: 'user',
              onClick: () => {
                this.props.history.push(`/app/detail/customer/${user.id}`)
              }
            })
          }
          {
            this.renderMember({ 
              nama: barbermen.nama,
              avatar: barbermen.avatar,
              grid: 'barbermen',
              onClick: () => {
                this.props.history.push(`/app/detail/barbermen/${barbermen.id}`)
              }
            })
          }
          {
            this.renderMember({ 
              nama: paketJasa.nama,
              avatar: paketJasa.avatar,
              grid: 'paket-jasa',
              onClick: () => {
                this.props.history.push(`/app/detail/paketjasa/${paketJasa.id}`)
              }
            })
          }
        </Grid>
      </Box>    
    )
  }
}

export default enchance(DetailSesi)