import React from 'react'
import { Box, Button, CheckBox, Form, FormField, Layer, Text, TextArea } from 'grommet'
import { compose, withStateHandlers, toClass } from 'recompose'
import { GQLItemDataById, GQLUpdateItem } from '../../services'
import Loading from '../commons/Loading'

const EditForm = props => {
  return (
    <Layer modal onClickOutside={e => {
      props.close()
    }}>
      <Box pad='medium' gap='medium' width="600px">
        <Text weight='bold'>Edit Data Item</Text>

        <Form style={{
          maxHeight: '60vh',
          overflowY: 'scroll'
        }}>

          <FormField
            label="Nama"
            value={props.item.nama}
            onChange={event => props.setItemValue('nama', event.target.value)}
          >
          </FormField>

          <FormField
            label="Brand"
            value={props.item.brand}
            onChange={event => props.setItemValue('brand', event.target.value)}
          >
          </FormField>

          <FormField
            label="Dijual"
            htmlFor="dijual-check-box"
          >
            <CheckBox 
              id="dijual-check-box" 
              label="Di jual"
              checked={props.item.dijual}
              onChange={e => props.setItemValue('dijual', e.target.checked)}
            />
          </FormField>

          <FormField
            label="Harga Jual"
            value={props.item.hargaJual}
            type="number"
            onChange={event => props.setItemValue('hargaJual', parseInt(event.target.value))}
          >
          </FormField>

          <FormField
            label="Harga Beli"
            value={props.item.hargaBeli}
            type="number"
            onChange={event => props.setItemValue('hargaBeli', parseInt(event.target.value))}
          >
          </FormField>

          <FormField
            label="Satuan Berat"
            value={props.item.satuanBerat}
            onChange={event => props.setItemValue('satuanBerat', event.target.value)}
          >
          </FormField>

          <FormField
            label="Berat"
            type="number"
            value={props.item.berat}
            onChange={event => props.setItemValue('berat', parseFloat(event.target.value))}
          >
          </FormField>

          <TextArea
            placeholder="Deskripsi"
            value={props.item.deskripsi}
            onChange={event => props.setItemValue("deskripsi", event.target.value)}
          >
          </TextArea>

        </Form>

        <Button 
          label="Save"
          onClick={() => {
            props.mutate()
            props.close()
          }}
        >
        </Button>
      </Box>
    </Layer>
  )
}

const enchance = compose(
  GQLItemDataById,
  Loading,
  withStateHandlers(
    ({ data: { itemById } }) => ({
      item: itemById,
      id: itemById.id,
      gambar: null
    }),
    {
      setItemValue: ({ item, id }) => (key, value) => {
        item[key] = value
        return {
          id,
          item: {
            nama: item.nama,
            brand: item.brand,
            deskripsi: item.deskripsi,
            hargaJual: item.hargaJual,
            hargaBeli: item.hargaBeli,
            dijual: item.dijual,
            satuanBerat: item.satuanBerat,
            berat: item.berat
          }
        }
      }
    }
  ),
  GQLUpdateItem,
  toClass
)

export default enchance(EditForm)
