import React, { Component } from 'react'
import { Button, Box, Form, FormField, Layer, Text } from 'grommet'
import { GQLCreateCustomer, Ax } from '../../services'
import { DumbLoading } from '../commons/Loading'

class AddCustomer extends Component {

  state = {
    nama: '',
    username: '',
    avatarFile: null,
    avatar: '',
    isLoading: false
  }

  uploadFile = async () => {
    let fd = new FormData()
    fd.append('gambar', this.gambar.files[0])
    let result = await Ax.post('/v1/medias/single', fd)
          .then(resp => resp.data)
    return result
  }
  
  addCustomer = async () => {
    this.setState({
      isLoading: true
    })
    let { mutate } = this.props
    let gambarUrl = await this.uploadFile()
    let payload = {
      name: this.state.nama,
      avatar: gambarUrl
    }
    mutate({ variables: { ...payload } })
    this.props.done()
    this.setState({
      isLoading: false
    })
  }

  renderForm = () => (
    <Box 
      fill 
      pad='medium' 
      gap='small'
      style={{
        minWidth: '500px'
      }}
    >
    
    <Text 
        margin={{
          vertical: 'medium'
        }}
        weight='bold'
      >Input Data Customer</Text>

    <Form>

      <FormField
        label="Nama"
        value={this.state.nama}
        onChange={e => {
          this.setState({
            nama: e.target.value
          })
        }}
      ></FormField>

      <FormField
        label="Avatar"
        htmlFor="gambar"
      >
        <input 
          ref={(ref) => { this.gambar = ref }}
          type="file" id="gambar" name="gambar"/>
      </FormField>

      <Box direction='row' gap='small'>
        <Button color='blue' margin={{ vertical: 'small' }}
          onClick={this.addCustomer}
        >
          OK
        </Button>
        <Button color='red' margin={{ vertical: 'small' }}
          onClick={this.props.cancel}
        >
          Batal
        </Button>
      </Box>
    </Form>

    </Box>
  )

  renderLoading = () => (
    <Box 
      fill 
      pad='medium' 
      gap='small'
      style={{
        minWidth: '500px'
      }}
    >
      <DumbLoading/>
    </Box>
  )

  render () {
    let comp = this.state.isLoading ? this.renderLoading() : this.renderForm()
    return (
      <Layer modal={true}>
        {comp}
      </Layer>
    )
  }

}

export default GQLCreateCustomer(AddCustomer)