import React, { Component } from 'react'
import moment from 'moment'
import { Layer, Box, Button, Form, FormField, Text } from 'grommet'
import { G_ListBarbermen, GQLListPaketJasa, GQLOpenSession, getGlobalConfig } from '../../services'
import createSelect from '../commons/SelectWithAvatar'

const BarbermenSelect = createSelect(G_ListBarbermen, data => data.cabangById.listBarbermen)
const PaketJasaSelect = createSelect(GQLListPaketJasa, data => data.cabangById.listPaketJasa)

class AddSesi extends Component {

  state = {
    barbermen: {
      id: null,
      nama: '',
      avatar: ''
    },
    paketJasa: {
      id: null,
      nama: '',
      avatar: ''
    },
    tanggal: '',
    waktu: ''
  }

  openSession = () => {
    let config = getGlobalConfig()
    let start = moment(`${this.state.tanggal} ${this.state.waktu}`).toISOString()
    let payload = Object.assign({
      idCabang: config.ID_CABANG,
      idAddedBy: config.ID_USER
    }, {
      idBarbermen: this.state.barbermen.id,
      idForUser: this.props.idUser,
      idPaketJasa: this.state.paketJasa.id,
      start
    })
    this.props.mutate({ variables: { payload } })
    this.props.done()
  }

  render () {
    return (
      <Layer modal={true}>
        <Box 
          fill 
          pad='medium' 
          gap='small'
          style={{
            minWidth: '500px'
          }}
        >
          <Text 
            margin={{
              vertical: 'medium'
            }}
            weight='bold'
          >Input Data Sesi</Text>
          <Form>
            <Box gap='small'>
              <BarbermenSelect
                placeholder='Pilih barbermen'
                value={this.state.barbermen}
                onChange={e => {
                  this.setState({
                    barbermen: e.option
                  })
                }}
              />

              <PaketJasaSelect
                value={this.state.paketJasa}
                placeholder='Pilih Paket Jasa'
                onChange={e => {
                  this.setState({
                    paketJasa: e.option
                  })
                }}
              />

              <FormField
                label='Hari'
                name='tanggal'
                required
                type='date'
                value={this.state.tanggal}
                onChange={e => {
                  this.setState({
                    tanggal: e.target.value
                  })
                }}
              >
              </FormField>

              <FormField
                label='Waktu'
                name='waktu'
                required
                type='time'
                value={this.state.waktu}
                onChange={e => {
                  this.setState({
                    waktu: e.target.value
                  })
                }}
              />

              <Box direction='row' gap='small' fill={false}>
                <Button
                  label='Ok'
                  onClick={this.openSession}
                />
                <Button
                  label='Batal'
                  color='red'
                  onClick={this.props.cancel}
                />
              </Box>

            </Box>
          </Form>
        </Box>
      </Layer>
    )
  }

}

export default GQLOpenSession(AddSesi)