import React from 'react'
import { Box, DropButton, Text} from 'grommet'
import { Apps } from 'grommet-icons'
import { compose, mapProps } from 'recompose'
import Avatar from '../commons/Avatar'
import LinkText from '../commons/LinkText'

const enchanceUserRow = compose(
  mapProps(props => {
     let {
      avatar,
      nama,
      username,
      id,
      onOpenSession,
      onDeleteSession,
      onDetailSession
    } = props
    return {
      item: {
        avatar,
        nama,
        username,
        id
      },
      onOpenSession,
      onDetailSession,
      onDeleteSession
    }
  })
)

const UserRow = enchanceUserRow(({ item, onOpenSession, onDeleteSession, onDetailSession }) => (
  <Box direction='row' gap='small' align='center' 
    pad='xsmall'
    border={{
      side: 'bottom',
      size: 'xsmall'
    }}
  >
    <Avatar
      avatar={item.avatar}
    />
    <Box fill>
      <LinkText 
        size='small' 
        weight='bold' 
        text={item.nama}
        onClick={() => onDetailSession(item.id)}
      ></LinkText>
      <Text size='xsmall'>{item.username}</Text>
    </Box>
    <Box pad='small'>
      <DropButton
        plain
        dropContent={
          <Box>

            <Box
              pad='small'
              onClick={() => onOpenSession(item.id)}
            >Sesi Baru</Box>

            <Box 
              pad='small'
              onClick={() => onDetailSession(item.id)}
            >Detail</Box>

            <Box 
              pad='small'
              onClick={() => onDeleteSession(item.id)}
            >Delete</Box>

          </Box>
        }
      >
        <Apps/>
      </DropButton>
    </Box>
  </Box>
))

export default UserRow