import React, { Component, Fragment } from 'react'
import { Text, Clock } from 'grommet'
import moment from 'moment'

const SimpleEstimation = ({ time }) => {
  return (<Text>{time}</Text>)
}

const SimpleClock = ({ time }) => (
  <Clock
    time={time}
    run
    type='digital'
  />
)

const isTomorrow = (time) =>
  moment.duration(moment(time).diff(moment())).asHours() > 24

const isToday = (time) =>
  moment.duration(moment(time).diff(moment())).asHours() < 24

const isLate = (time) => 
  moment(time).isBefore(moment())

const calculateRealElapsedTime = (time) => {
  let t = moment()
  return moment(t.diff(time)).toISOString()
}

const calculateSimpleElapssedTime = (time) =>
  moment().to(time)

const calculateRemainingTime = (time) =>
  moment.duration( moment(time).diff(moment()) ).toISOString()

const formatAnchor = s => moment(s).format('MMM D, HH:mm')

const calculateState = ({ state, time }) => {
  if (state === 'DONE') {
    return {
      clock: false,
      current: calculateSimpleElapssedTime(time.executionEndTime),
      anchor: 'Berakhir: ' + moment(time.executionEndTime).calendar(moment())
    }
  }

  if (state === 'CANCELED') {
    return {
      clock: false,
      current: calculateSimpleElapssedTime(time.scheduledStartTime),
      anchor: 'Seharusnya Dimulai: ' + moment(time.scheduledStartTime).calendar(moment())
    }
  }

  if (state === 'ONGOING') {
    return {
      clock: true,
      current: calculateRealElapsedTime(time.executionStartTime),
      anchor: 'Dimulai: ' + moment(time.executionStartTime).calendar(moment())
    }
  }

  if (state === 'SCHEDULED') {

    if (isLate(time.scheduledStartTime)) { 
      return {
        clock: true,
        current: calculateRealElapsedTime(time.scheduledStartTime),
        anchor: 'Jadwal mulai: ' + moment(time.scheduledStartTime).calendar(moment())
      }
    }
    if (isTomorrow(time.scheduledStartTime)) { 
      return {
        clock: false,
        current: calculateSimpleElapssedTime(time.scheduledStartTime),
        anchor: 'Akan dimulai: ' + formatAnchor(time.scheduledStartTime)
      }
    }
    if (isToday(time.scheduledStartTime)) {
      return {
        clock: true,
        current: calculateRemainingTime(time.scheduledStartTime),
        anchor: 'Akan dimulai ' + formatAnchor(time.scheduledStartTime)
      }
    }
  }
}


export default class TimeInfo extends Component {

  state = {
    current: moment().toISOString(),
    clock: true,
    anchor: ''
  }

  componentDidMount () {
    this.intervalId = setInterval(() => {
      let { clock, current, anchor } = calculateState(this.props)
      this.setState({
        clock,
        current,
        anchor
      })
    }, 1000);
  }

  componentWillUnmount () {
    if (this.intervalId) {
      clearInterval(this.intervalId)
    }
  }

  render () {
    let result
    if (this.state.clock) {
      result = (<SimpleClock time={this.state.current}/>)
    } else {
      result = (<SimpleEstimation time={this.state.current}/>)
    }
    return (
      <Fragment>
        {result}
        <Text size='xsmall'>{this.state.anchor}</Text>
      </Fragment>
    )
  }

}
