import React, { Component } from 'react'
import { Layer, Box, Text } from 'grommet'
import { Archlinux } from 'grommet-icons'
import { withRouter } from 'react-router-dom'

const menus = [
  {
    name: 'Transaksi',
    to: '/app/transaksi'
  },
  {
    name: 'Sesi',
    to: '/app/sesi/all'
  },
  {
    name: 'Search',
    to: '/app/search/customer'
  },
  {
    name: 'Inventaris',
    to: '/app/inventory'
  }
];

const MenuBoxItem = withRouter((props) => {
  return (
    <Box 
      justify='center'
      pad={{
        horizontal: 'medium'
      }}
      onClick={() => props.history.push(`${props.to}`)}
      style={{
        cursor: 'pointer'
      }}
    >
      <Text margin='none' size='small'>
        {props.name}
      </Text>
    </Box>
  )
})

class Navigation extends Component {
  render () {
    return (
      <Layer
        position='top'
        full='horizontal'
        modal={false}
      >

          <Box
            fill 
            background='white'
            pad={{
              vertical: 'small',
              horizontal: 'medium'
            }}
            direction='row'
            style={{
              boxShadow: '0 0 12px #888'
            }}
          >
            <Box 
              direction='row' 
              gap='medium'
              align='center'
              fill
              onClick={this.props.logoClick}
            >
              <Archlinux color='brand' size='medium' />
              <Text margin='none'>
                Manliest
              </Text>
            </Box>

            <Box direction='row' justify='end' width='1000px'>
              { menus.map((m, index) => <MenuBoxItem key={'box-nav-' + index} {...m} />) }
            </Box>
          </Box>

      </Layer>
    )
  }
}

export default Navigation;