import React, { Component } from 'react'
import { Box, Text, DropButton } from 'grommet'
import { Down } from 'grommet-icons'
import { GQLMutationById } from '../../services'
import Loading from '../commons/Loading'
import { compose, mapProps, withState } from 'recompose'
import { withRouter } from 'react-router-dom'
import moment from 'moment'
import EditMutation from './EditMutation'

const enchance = compose(
  withRouter,
  mapProps(({ match }) => {
    return {
      id: parseInt(match.params.id)
    }
  }),
  GQLMutationById,
  Loading,
  mapProps( ({ data }) => {
    console.log(data)
    return {
      item: data.mutationById
    }
  }),
  withState('mode', 'setMode', 'read')
)

class DetailMutation extends Component {

  renderRow = (key, value) => {
    return (
      <Box 
        direction='row' 
        justify='between'
        pad='small'
        border={{
          side: 'bottom',
          size: 'xsmall',
          color: 'light-3'
        }}
      >
        <Text>{key}: </Text>
        <Text>{value}</Text>
      </Box>
    )
  }

  render () {
    let { mode, setMode } = this.props
    return (
      <Box gap='medium'>

        {
          (mode === 'edit') &&
          (
            <EditMutation 
              id={this.props.item.id} 
              close={e => {
                setMode('read')
              }}
            />
          )
        }
        <Box 
          direction='row'
          align='center'
          gap='medium'
        >
          <Text weight='bold'>Detail Mutasi #{this.props.item.id}</Text>
          <DropButton
            icon={<Down/>}
            dropContent={
              <Box pad='small' gap='small'>
                <Box onClick={() => setMode('edit')}>
                  <Text>Edit</Text>
                </Box>
                <Box>
                  <Text>Delete</Text>
                </Box>
              </Box>
            }
          />
        </Box>

        <Box gap='small' width='600px'>
            {this.renderRow('Tipe', this.props.item.__typename)}
            {this.renderRow('Item', this.props.item.item.nama)}
            {this.renderRow('Waktu', moment(this.props.item.waktu).format('DD-MM-YYYY h:mm a')) }
            {this.renderRow('Nominal', this.props.item.nominal)}
            {this.renderRow('Jumlah', this.props.item.jumlah)}
        </Box>

      </Box>
    )
  }

}

export default enchance(DetailMutation)