import React from 'react'
import { Box } from 'grommet'
import { compose, withState, withHandlers } from 'recompose'
import AddSesi from './AddSesi'
import UserList from './UserList'
import { withRouter } from 'react-router-dom'

const enchanceUserList = compose(
  withState('activeUserId', 'setActiveUserId', null),
  withState('showAddLayer', 'setShowAddLayer', false),
  withRouter,
  withHandlers({
    onOpenSession: props => id => {
      console.log('SHOW OPEN SESSION DIALOG')
      props.setActiveUserId(id)
      props.setShowAddLayer(true)
    },
    onDetailSession: props => id => {
      props.history.push(`/app/detail/customer/${id}`)
    },
    onDeleteSession: props => event => {
      console.log('DELETE USER')
    }
  })
)



const UserContainer = enchanceUserList((props) => (
  <Box 
    gap='medium'
  >
    {
      props.showAddLayer &&
      <AddSesi
        idUser={props.activeUserId}
        done={() => {
          props.setShowAddLayer(false)
        }}
        cancel={() => {
          props.setShowAddLayer(false)
        }}
      />
    }

    <UserList
      onOpenSession={props.onOpenSession}
      onDetailSession={props.onDetailSession}
      onDeleteSession={props.onDeleteSession}
      keyword={props.keyword}
    />
  </Box>
))

export default UserContainer