import React, { Component } from 'react'
import { Box, Text } from 'grommet'
import { GQLStartSession, GQLEndSession, GQLCancelSession, GQLDeleteSession } from '../../services'
import { withRouter } from 'react-router-dom'

const MenuBox = action => ({ id, mutate, doneDelete }) => {
  return (
    <Box pad='small'
      onClick={() => {
        mutate()
        if (doneDelete) {
          doneDelete()
        }
      }}
    >
      <Text size='small'>{action}</Text>
    </Box>
  )
}

const StartBox = GQLStartSession(MenuBox('Start'))
const DoneBox = GQLEndSession(MenuBox('Done'))
const CancelBox = GQLCancelSession(MenuBox('Cancel'))
const DeleteBox = GQLDeleteSession(MenuBox('Delete'))

const OtherBox = name => (props) => (
  <Box pad='small'>
    <Text size='small'>{name}</Text>
  </Box>
)

const DetailBox = withRouter( ({ history, id }) => (
  <Box pad='small'
    onClick={() => {
      history.push(`/app/detail/sesi/${id}`)
    }}
  >
    <Text size='small'>Detail</Text>
  </Box>
))

const OpenMenus = ({state, ...rest}) => {
  let result = [
    (<DetailBox {...rest} />),
    (<DeleteBox {...rest}/>)
  ]
  if (state === 'SCHEDULED') {
    return result.concat([
      (<StartBox {...rest} />),
      (<CancelBox state {...rest}/>)
    ])
  }
  if (state === 'ONGOING') {
    return result.concat([
      (<DoneBox state={state} {...rest}/>)
    ])
  }
  return result
}

export default class RowMenuBox extends Component {

  render () {
    return (
      <Box>
        {OpenMenus(this.props)}
      </Box>
    )
  }

}

