import React, { Component } from 'react'
import { Box, Button, Text } from 'grommet'
import { GQLListPaketJasa } from '../../services'
import { compose } from 'recompose'
import Loading from '../commons/Loading'
import Avatar from '../commons/Avatar'
import { withRouter } from 'react-router-dom'

const enchance = compose(
  withRouter,
  GQLListPaketJasa,
  Loading
)

class PaketJasa extends Component {
  
  renderRow = (row) => {
    return (
      <Box direction='row' gap='small' align='center' 
        pad='xsmall'
        border={{
          side: 'bottom',
          size: 'xsmall'
        }}
      >
        <Avatar
          avatar={row.avatar}
        />
        <Box 
          style={{
            minWidth: '200px',
            maxHeight: '200px'
          }}
        >
          <Button 
            label={row.nama}
            plain
            onClick={() => {
              this.props.history.push(`/app/detail/paketjasa/${row.id}`)
            }}
          />
          <Text size='small'>{row.harga} IDR - {row.estimasiWaktu} menit</Text>
        </Box>

        <Box 
        >
          <Text size='small' weight='bold'>{row.items.join(' - ')}</Text>
          <Text size='small'>{row.keterangan}</Text>
        </Box>
      </Box>
    )
  }

  filterByKeyword = (row) => {
    return row.nama.toUpperCase().includes(this.props.keyword.toUpperCase())
  }

  render () {

    let { data: { cabangById: { listPaketJasa } } } = this.props
    let items = listPaketJasa.filter(this.filterByKeyword).map(this.renderRow)

    return (
      <Box 
        margin={{
          vertical: 'large'
        }}
        gap='medium'
      >
        {items}
      </Box>
    )
  }

}

export default enchance(PaketJasa)