import React, { Component } from 'react'
import { Button, Box, Form, FormField, Layer, Text } from 'grommet'
import { GQLCreateBarbermen, getGlobalConfig, Ax } from '../../services'
import { DumbLoading } from '../commons/Loading'

class AddBarbermen extends Component {

  state = {
    nama: '',
    username: '',
    avatarFile: null,
    avatar: '',
    isLoading: false
  }

  uploadFile = async () => {
    let fd = new FormData()
    fd.append('gambar', this.gambar.files[0])
    let result = await Ax.post('/v1/medias/single', fd)
          .then(resp => resp.data)
    return result
  }
  
  addBarbermen = async () => {
    this.setState({ isLoading: true })
    try {
      let { mutate } = this.props
      let { ID_CABANG } = getGlobalConfig()
      let gambarUrl = await this.uploadFile()
      let payload = {
        nama: this.state.nama,
        idCabang: ID_CABANG,
        avatar: gambarUrl
      }
      console.log(payload)
      mutate({ variables: { payload } })
    } catch(err) {
      console.log(err)
      alert('Gagal')
    }
    this.setState({ isLoading: false })
    this.props.done()
  }

  renderLoading = () => (
    <Box 
      fill 
      pad='medium' 
      gap='small'
      style={{
        minWidth: '500px'
      }}
    >
      <DumbLoading/>
    </Box>
  )

  renderComponent = () => (
    <Box 
      fill 
      pad='medium' 
      gap='small'
      style={{
        minWidth: '500px'
      }}
    >
      
      <Text 
          margin={{
            vertical: 'medium'
          }}
          weight='bold'
        >Input Data Barbermen</Text>

      <Form>

        <FormField
          label="Nama"
          value={this.state.nama}
          onChange={e => {
            this.setState({
              nama: e.target.value
            })
          }}
        ></FormField>

        <FormField
          label="Avatar"
          htmlFor="gambar"
        >
          <input 
            ref={(ref) => { this.gambar = ref }}
            type="file" id="gambar" name="gambar"/>
        </FormField>

        <Box direction='row' gap='small'>
          <Button color='blue' margin={{ vertical: 'small' }}
            onClick={this.addBarbermen}
          >
            OK
          </Button>
          <Button color='red' margin={{ vertical: 'small' }}
            onClick={this.props.cancel}
          >
            Batal
          </Button>
        </Box>
      </Form>
    </Box>
  )

  render () {
    let comp = this.state.isLoading ? this.renderLoading() : this.renderComponent()
    return (
      <Layer modal={true}>
        {comp}
      </Layer>
    )
  }

}

export default GQLCreateBarbermen(AddBarbermen)