import React, { Component } from 'react'
import { Box, Text } from 'grommet'

const tabsOptions = {
  direction: 'row',
  align: 'start',
  fill: true,
  style: {
    maxWidth: '600px'
  }
}


const Tab = ({ active, name, textSize, ...props }) => {

  let border = {
    side: 'bottom',
    size: '0px',
    color: 'neutral-3'
  }
  let _ts = 'small'
  if (textSize) {
    _ts = textSize
  }

  if (active) border.size = 'small'

  return (
    <Box
      border={border}
      fill
      pad='medium'
      justify='center'
      align='center'
      {...props}
    >
      <Text size={_ts} weight='bold'>{name}</Text>
    </Box>
  )
}

export default class AppTabs extends Component {
  
  state = {
    active: null
  }

  onActiveChange = (v) => {
    this.setState({
      active: v
    })
    this.props.onChange(v)
  }

  render () {
    let { items, ...rest } = this.props
    let options = Object.assign({}, tabsOptions, rest)
    let tabs = items.map((name, i) => {
      return (
        <Tab
          key={i}
          name={name}
          active={name === this.state.active}
          textSize={rest.textSize}
          onClick={() => this.onActiveChange(name)}
        />
      )
    })
    return (<Box
      direction='row'
      {...options}
    >
      {tabs}
    </Box>)
  }
}