import React from 'react'
import { Box, Text } from 'grommet'

export default ({ state, pad, size }) => {
  let infoBackground = 'neutral-2', infoText = 'Open';
  switch (state) {
    case 'DONE':
      infoBackground = 'status-ok'
      infoText = 'Done'
      break
    case 'ONGOING':
      infoBackground = 'status-warning'
      infoText = 'On Going'
      break
    case 'CANCELED':
      infoBackground = 'status-error'
      infoText = 'Cancelled'
      break
    default:
      break;
  }

  return (
    <Box pad={pad} background={infoBackground}>
      <Text weight='bold' size={size}>
        {infoText}
      </Text>
    </Box>
  )
}