import React from 'react'
import { Box } from 'grommet'
import { DEF_IMAGE } from '../../services'

const defSize = '42px'
const Avatar = ({ avatar, size, round }) => {
  let url = (!avatar || avatar === '') ? DEF_IMAGE : avatar
  let avSize = !size ? defSize : size
  let avRound = !round ? 'full' : round
  return (
    <Box 
      background={`url(${url})`} 
      round={avRound}
      style={{
        minWidth: avSize,
        minHeight: avSize,
        maxWidth: avSize,
        maxHeight: avSize
      }}
    />
  )
}

export default Avatar