import React from 'react'
import { Select, Box, Text } from 'grommet'
import { compose, mapProps } from 'recompose'

const notSuccessThenEmpty = key => ({ data, ...rest }) => {
  let options = []
  let result = {
    options,
    ...rest
  }
  if (!data) return result
  if (data.networkStatus === 7) {
    result.options = key(data)
  }
  return result
}

const OptionBox = ({ id, nama, avatar }) => {
  return (
    <Box fill='horizontal' 
      pad='xsmall' 
      direction='row'
      justify='start'
      align='center'
      gap='small'
    >
      <Box
        background={`url(${avatar})`}
        style={{
          width: '38px',
          height: '38px'
        }}
      />
      <Text size='small'>{nama}</Text>
    </Box>
  )
}

const createSelect = (GQLQuery, extract) => 
  compose(
    GQLQuery,
    mapProps(notSuccessThenEmpty(extract))
  )(
    (props) => {
      const { placeholder, options, onChange, value, ...rest } = props
      return (
        <Select
          options={options}
          children={OptionBox}
          placeholder={placeholder}
          onChange={onChange}
          value={value}
          labelKey='nama'
          {...rest}
        >
        </Select>
      )
    }
  )

export default createSelect
