import React from 'react'
import moment from 'moment'
import { Box, Layer, Text, TextInput, Button, FormField } from 'grommet'
import { Edit as Pencil } from 'grommet-icons'
import { withState } from 'recompose'

const FilterTime = (props) => {
  let { start, end, setStart, setEnd, showLayer, setShowLayer } = props
  let fs = start.format('DD/MM/YYYY')
  let fe = end.format('DD/MM/YYYY')
  return (
    <Box gap='small' direction='row' align='center'>
      { showLayer &&
        <Layer
          modal
          style={{
            minWidth: '500px'
          }}
          onClickOutside={() => {
            setShowLayer(false)
          }}
        >
          <Box fill pad='medium' gap='medium'>

            <FormField
              label='Start'
            >
              <TextInput
                value={start.format('YYYY-MM-DD')}
                type='date'
                onChange={e => {
                  setStart(moment(e.target.value))
                }}
              />
            </FormField>

            <FormField
              label='End'
            >
              <TextInput
                value={end.format('YYYY-MM-DD')}
                type='date'
                onChange={e => {
                  setEnd(moment(e.target.value))
                }}
              />
            </FormField>

          </Box>
        </Layer>
      }
      <Text size='small'>
        Menampilkan data dari tanggal {fs} sampai {fe}
      </Text>
      <Button
        plain
        icon={<Pencil size='small'/>}
        onClick={event => {
          setShowLayer(true)
        }}
      />
    </Box>
  )
}

export default withState('showLayer', 'setShowLayer', false)(FilterTime)