import React, { Component } from 'react'
import { Button, Box, Form, FormField, Layer, Select, Text } from 'grommet'
import { GQLCreatePaketJasa, getGlobalConfig, Ax } from '../../services'
import { DumbLoading } from '../commons/Loading'

const subItems = ['Cukur', 'Wax', 'Gunting', 'Layanan 1', 'Layanan 2', 'Layanan 3']

class AddPaketJasa extends Component {

  state = {
    nama: '',
    avatar: '',
    items: subItems.map(nama => ({ nama })),
    selectedItems: [],
    estimasiWaktu: 30,
    harga: 50000,
    isLoading: false
  }

  uploadFile = async () => {
    let fd = new FormData()
    fd.append('gambar', this.gambar.files[0])
    let result = await Ax.post('/v1/medias/single', fd)
          .then(resp => resp.data)
    return result
  }

  addPaketJasa = async () => {
    this.setState({ isLoading: true })
    try {
      let { mutate } = this.props
      let { ID_CABANG } = getGlobalConfig()
      let gambarUrl = await this.uploadFile()
      let payload = {
        nama: this.state.nama,
        listCabang: [ID_CABANG],
        estimasiWaktu: this.state.estimasiWaktu,
        harga: this.state.harga,
        items: this.state.selectedItems.map(({ nama }) => nama),
        avatar: gambarUrl
      }
      console.log(payload)
      mutate({ variables: { payload } })
    } catch(err) {
      console.log(err)
      alert('Gagal')
    }
    this.setState({ isLoading: false })
    this.props.done()
  }

  subItemOptionBox = (option, index, _, state) => {
    let background = 'white'
    if (state.selected) {
      background = 'neutral-3'
    }
    return (
      <Box 
        fill='horizontal' 
        pad='small'
        direction='row'
        justify='start'
        align='center'
        gap='small'
        background={background}
      >
        <Text size='small'>{option.nama}</Text>
      </Box>
    )
  }

  subItemSelect = () => {
    return (
      <Box gap="small">
        <Box direction='row' wrap>
          {this.state.selectedItems.map(it => {
            return (
              <Box 
                pad={{ vertical: 'xsxmall', horizontal: 'small' }} 
                margin={{ horizontal: 'small' }}
                background='neutral-3'>
                <Text>{it.nama}</Text>
              </Box>
            )
          })}
        </Box>
        <Select
          options={this.state.items}
          children={this.subItemOptionBox}
          placeholder='Pilih Item'
          onChange={event => {
            let opt = event.option
            let curr = this.state.selectedItems
            curr.push(opt)
            this.setState({
              selectedItems: curr
            })
          }}
          value={this.state.selectedItems}
          multiple='true'
          labelKey='nama'
          valueKey='nama'
        />
      </Box>
    )
  }

  renderLoading = () => {
    return (
      <Box 
        fill 
        pad='medium' 
        gap='small'
        style={{
          minWidth: '500px'
        }}
      >
        <DumbLoading/>
      </Box>
    )
  }

  renderComponent = () => (
    <Box 
      fill 
      pad='medium' 
      gap='small'
      style={{
        minWidth: '500px'
      }}
    >
      
      <Text 
          margin={{
            vertical: 'medium'
          }}
          weight='bold'
        >Input Data Paket Jasa</Text>

      <Form>

        <FormField
          label="Nama"
          value={this.state.nama}
          onChange={e => {
            this.setState({
              nama: e.target.value
            })
          }}
        ></FormField>
        
        {this.subItemSelect()}

        <FormField
          label="Estimasi Waktu"
          type="number"
          min="1"
          max="120"
          value={this.state.estimasiWaktu}
          onChange={e => {
            this.setState({
              estimasiWaktu: e.target.value
            })
          }}
        ></FormField>

        <FormField
          label="Harga"
          type="number"
          min="0"
          max="500000"
          value={this.state.harga}
          onChange={e => {
            this.setState({
              harga: e.target.value
            })
          }}
        ></FormField>

        <FormField
          label="Avatar"
          htmlFor="gambar"
        >
          <input 
            ref={(ref) => { this.gambar = ref }}
            type="file" id="gambar" name="gambar"/>
        </FormField>

        <Box direction='row' gap='small'>
          <Button 
            margin={{ vertical: 'small' }}
            onClick={this.addPaketJasa}
          >
            OK
          </Button>
          <Button color='red' margin={{ vertical: 'small' }}
            onClick={this.props.cancel}
          >
            Batal
          </Button>
        </Box>
      </Form>
    </Box>
  )

  render () {
    let comp = this.state.isLoading ? this.renderLoading() : this.renderComponent()
    return (
      <Layer modal={true} onClickOutside={e => this.props.cancel()}>
        {comp}
      </Layer>
    )
  }

}

export default GQLCreatePaketJasa(AddPaketJasa)