import React from 'react'
import { Box, DropButton, Text } from 'grommet'
import { Down } from 'grommet-icons'
import { GQLItemById } from '../../services'
import Loading from '../commons/Loading'
import Avatar from '../commons/Avatar'
import Tabs from '../commons/Tabs'
import { compose, mapProps, withState } from 'recompose'
import { withRouter, Route } from 'react-router-dom'
import ItemMutations from './ItemMutations'
import EditItem from './EditItem'
import BuyMoreItem from './BuyMoreItem'
import UseItem from './UseItem'

const enchance = compose(
  withRouter,
  mapProps(({ match, ...rest }) => ({
    id: parseInt(match.params.id),
    match,
    ...rest,
  })),
  withState('skip', 'setSkip', 0),
  withState('take', 'setTake', 30),
  GQLItemById,
  Loading,
  mapProps( ({ data, match, history }) => ({
    item: data.itemById,
    urls: {
      mutations: () => history.push(`${match.url}/mutations`),
      pictures: () => history.push(`${match.url}/pictures`)
    }
  }) ),
  withState('mode', 'setMode', 'read')
)

const descriptionRowOptions = {
  direction: 'row',
  justify: 'between',
  align: 'center',
  pad: 'xsmall',
  border: {
    side: 'bottom',
    size: 'xsmall',
    color: '#333'
  }
}

const DetailItem = props => {
  return (
    <Box>
      
      {/* Edit Form */}
      {
        (props.mode==='edit') && 
          (<EditItem id={props.item.id}
              close={e => props.setMode('read')}
          />)
      }
      {
        (props.mode==='use-item') && 
          (<UseItem 
              idItem={props.item.id}
              close={e => props.setMode('read')}
          />)
      }
      {
        (props.mode==='buy-more') && 
          (<BuyMoreItem 
              idItem={props.item.id}
              close={e => props.setMode('read')}
          />)
      }

      <Box 
        direction='row' 
        align='center'
        pad={{
          vertical: 'medium'
        }}>
        <Text
          weight='bold'
        >
          Detail Item #{props.item.id}
        </Text>

        <DropButton
          icon={<Down/>}
          dropContent={
            <Box pad='small'>
              <Box pad='small' onClick={() => props.setMode('edit')}>
                <Text>Edit</Text>
              </Box>
              <Box pad='small' onClick={() => props.setMode('buy-more')}>
                <Text>Buy More Item</Text>
              </Box>
              <Box pad='small' onClick={() => props.setMode('use-item')}>
                <Text>Use Item</Text>
              </Box>
              <Box pad='small'>
                <Text>Delete</Text>
              </Box>
            </Box>
          }
        />

      </Box>

      <Box direction='row' 
        align='start'
        gap='large' 
        pad={{
          horizontal: 'large',
          vertical: 'small'
        }}
      >
        <Box align='center' gap='small'>
          <Box pad="small" background='neutral-3'>
            <Avatar
              size='120px'
              round="false"
              avatar={props.item.avatar}
            />
          </Box>
          <Text size='large' weight='bold'>{props.item.nama}</Text>
          <Box round='medium' background='neutral-1' pad='small'>
            <Text weight='bold' color='white'>
              {props.item.stock}
            </Text>
          </Box>
        </Box>
        <Box gap='small' style={{ minWidth: '400px', maxWidth: '450px' }}>
          <Box {...descriptionRowOptions}>
            <Text size='small'>Nama</Text>
            <Text size='small' weight='bold'>{props.item.nama}</Text>
          </Box>
          <Box {...descriptionRowOptions}>
            <Text size='small'>Brand</Text>
            <Text size='small' weight='bold'>{props.item.brand}</Text>
          </Box>
          <Box {...descriptionRowOptions}>
            <Text size='small'>Harga Beli</Text>
            <Text size='small' weight='bold'>{props.item.hargaBeli} IDR</Text>
          </Box>
          <Box {...descriptionRowOptions}>
            <Text size='small'>Harga Jual</Text>
            <Text size='small' weight='bold'>{props.item.hargaJual} IDR</Text>
          </Box>
          <Box pad='small'>
            <Text size='small'>
              {props.item.deskripsi}
            </Text>
          </Box>
        </Box>
      </Box>

      {/* List of mutations goes here */}
      <Box
        justify='center'
        border={{
          side: 'bottom',
          color: '#444',
          size: 'small'
        }}
      >
        <Tabs
          textSize='small'
          items={[
            'Mutasi',
            'Pictures'
          ]}
          onChange={e => {
            switch (e) {
              case 'Mutasi':
                props.urls.mutations()
                break;
              case 'Pictures':
                props.urls.pictures()
                break;
              default:
                break;
            }
          }}
          style={{
            maxWidth: '200px'
          }}
        >
        </Tabs>
        <Route 
          path="/app/detail/item/:id/mutations"
          render={() => (<ItemMutations items={props.item.mutations}/>)}
        ></Route>
      </Box>
    </Box>
  )
}

export default enchance(DetailItem)