import React from 'react'
import { Fragment, Component } from 'react'
import { Box } from 'grommet'
import { Route } from 'react-router-dom'

import Sidebar from './commons/Sidebar'
import Navigation from './commons/Navigation'
import Sesi from './sesi/Sesi'
import Search from './search/Main'
import Detail from './detail/Main'
import Inventory from './inventory'

const Container = (props) => (
  <Box
    fill
    style={{
      minHeight: '100vh' // Make sure it stretch along y axis
    }}
  >
    {props.children}
  </Box>
);


export default class MainApp extends Component {

  state = {
    sidebarWidth: 300,
    sidebar: true
  }

  render () {

    const sidebarWidth = this.state.sidebarWidth
    const contentLeftPad = sidebarWidth + 20

    return (
      <Container>
        <Fragment>
          <Navigation logoClick={() => this.setState({ sidebar: !this.state.sidebar })}/>
          { this.state.sidebar && <Sidebar width={sidebarWidth}/> }
          <Box 
            fill
            margin={{
              top: '60px'
            }}
            pad={{
              horizontal: 'medium',
              left: `${contentLeftPad}px`
            }}
          >
            <Route path="/app/sesi/:type" component={Sesi}/>
            <Route path="/app/search" component={Search}/>
            <Route path="/app/detail" component={Detail}/>
            <Route path="/app/inventory" component={Inventory}/>
          </Box>
        </Fragment>  
      </Container>
    )
  }
}
