import React, { Component } from 'react'
import { Box, Button } from 'grommet'
import { G_ListBarbermen } from '../../services'
import Loading from '../commons/Loading'
import Avatar from '../commons/Avatar'
import { compose } from 'recompose'
import { withRouter } from 'react-router-dom'

const enchance = compose(
  G_ListBarbermen,
  Loading
)

const BarbermenRow = withRouter(props => (
  <Box direction='row' gap='small' align='center' 
    pad='xsmall'
    border={{
      side: 'bottom',
      size: 'xsmall'
    }}
  >
    <Avatar
      avatar={props.avatar}
    />
    <Button
      label={props.nama}
      plain
      onClick={() => {
        props.history.push(`/app/detail/barbermen/${props.id}`)
      }}
    >
    </Button>
  </Box>
))

let BarbermenList = ({ keyword, data: { cabangById: { listBarbermen } } }) => (
  <Box 
    gap='medium'
  >
    { 
      listBarbermen
        .filter(it => {
          return it.nama.toUpperCase().includes(keyword.toUpperCase())
        })
        .map((it, index) => 
          <BarbermenRow
            key={`barb-row#${index}`}
            {...it}
          />
        )
    }
  </Box>
)

BarbermenList = enchance(BarbermenList)

export default class Barbermen extends Component {
  render () {
    return (
      <Box>
        <BarbermenList keyword={this.props.keyword} />
      </Box>
    )
  }
}