import React, { Component } from 'react'
import { Box, CheckBox, Grid, Layer, Text, Button, DropButton, Form, FormField } from 'grommet'
import { Apps } from 'grommet-icons'
import { GQLListItem, GQLBuyNewItem, getGlobalConfig } from '../../services'
import { compose, mapProps, withState } from 'recompose'
import Loading from '../commons/Loading'
import LinkText from '../commons/LinkText'
import Avatar from '../commons/Avatar'
import { withRouter } from 'react-router-dom'

const enchanceList = compose(
  GQLListItem,
  Loading,
  mapProps(props => {
    return {
      items: props.data.listItem
    }
  }),
  withRouter
)

const enchanceInventory = compose(
  withState('skip', 'setSkip', 0),
  withState('take', 'setTake', 30),
  withState('isAdd', 'setIsAdd', false)
)

class AddForm extends Component {

  state = {
    item: {
      nama: "Foobar 2",
      avatar: "",
      brand: "",
      dijual: false,
      deskripsi: "sdjksd sdsd sdsd",
      jumlah: 5,
      nominal: 500000,
      hargaBeli: 90000,
      hargaJual: 100000,
      satuanBerat: 'gr',
      berat: 1200
    }
  }

  setItemState = key => value => {
    let current = this.state.item
    current[key] = value
    this.setState({
      item: current
    })
  }

  render() {
    return (
      <Layer modal onClickOutside={this.props.close}>
        <Box
          pad='small'
          gap='small'
          style={{
            minWidth: '500px'
          }}
        >
          <Text weight='bold'>Input Data Item</Text>
          
          <Form style={{
            maxHeight: '70vh',
            overflowY: 'scroll'
          }}>

            <FormField
              label="Nama"
              type="text"
              value={this.state.item.nama}
              onChange={e => { this.setItemState('nama')(e.target.value) }}
            ></FormField>

            <FormField
              label="Deskripsi"
              value={this.state.item.deskripsi}
              onChange={e => this.setItemState('deskripsi')(e.target.value)}
            />

            <FormField
              label="Harga beli"
              type="number"
              min="0"
              value={this.state.item.hargaBeli}
              onChange={e => this.setItemState('hargaBeli')(e.target.value)}
            />

            <FormField label="Label" htmlFor="dijual-check-box">
              <Box pad='small'>
                <CheckBox 
                  id="dijual-check-box" 
                  label="Di jual"
                  checked={this.state.item.dijual}
                  onChange={e => this.setItemState('dijual')(e.target.checked)}
                />
              </Box>
            </FormField>

            <FormField
              label="Harga Jual"
              min="0"
              type="number"
              value={this.state.item.hargaJual}
              onChange={e => this.setItemState('hargaJual')(e.target.value)}
            />

            <FormField
              label="Jumlah"
              min="0"
              type="number"
              value={this.state.item.jumlah}
              onChange={e => this.setItemState('jumlah')(e.target.value)}
            />

            <FormField
              label="Nominal"
              type="number"
              min="0"
              value={this.state.item.nominal}
              onChange={e => this.setItemState('nominal')(e.target.value)}
            />

            <FormField
              label="satuanBerat"
              value={this.state.item.satuanBerat}
              onChange={e => this.setItemState('satuanBerat')(e.target.value)}
            />

            <FormField
              label="berat"
              type="number"
              value={this.state.item.berat}
              onChange={e => this.setItemState('berat')(e.target.value)}
            />
          </Form>

          <Button
            label='Simpan'
            onClick={e => {
              let payload = {
                idCabang: getGlobalConfig().ID_CABANG,
                idAddedBy: getGlobalConfig().ID_USER,
                ...this.state.item
              }
              this.props.mutate({
                variables: { payload }
              })
              this.props.done()
            }}
            style={{
              maxWidth: '100px'
            }}
          >
          </Button>
        
        </Box>
      </Layer>
    )
  }
}

const ItemRow = withRouter(props => {
  return (
    <Box 
      border={{
        side: 'bottom',
        size: 'xsmall',
        color: 'light-4'
      }}
      background='light-2'
    >
      <Grid
        gap='medium'
        columns={['80px', '100px', 'flex', 'auto']}
        rows={['flex']}
        areas={[
          { name: 'stock', start: [0, 0], end: [0,0] },
          { name: 'image', start: [1, 0], end: [1,0] },
          { name: 'description', start: [2, 0], end: [2,0] },
          { name: 'actions', start: [3, 0], end: [3,0] }
        ]}
        fill
      >
        <Box
          gridArea='stock'
          justify='center'
          align='center'
        >
          <Text weight='bold' size='large'>{props.item.stock}</Text>
        </Box>
        <Box
          gridArea='image'
          justify='center'
          align='center'
        >
          <Avatar avatar={props.item.avatar} round='false' />
        </Box>
        <Box 
          gridArea='description'
          justify='center'
          align='start'
        >
          <LinkText 
            weight='bold' 
            size='small'
            text={props.item.nama}
            onClick={() => {
              props.history.push(`/app/detail/item/${props.item.id}`)
            }}
          >
          </LinkText>
          <Text size='xsmall'>Harga Beli: {props.item.hargaBeli} IDR</Text>
          <Text size='xsmall'>Harga Jual: {props.item.hargaJual} IDR</Text>
        </Box>
        <Box justify='end' gridArea='actions' pad='small'>
          <DropButton plain
            icon={<Apps/>}
            dropContent={
              <Box>
                <Box pad='small'>
                  <Button 
                    plain 
                    label='Detail'
                    onClick={() => {
                      props.history.push(`/app/detail/item/${props.item.id}`)
                    }}
                  />
                </Box>
                <Box pad='small'>
                  <Text>Buy more item</Text>
                </Box>
              </Box>
            }
          >
          </DropButton>
        </Box>
      </Grid>

    </Box>  
  )
})

const ItemList = enchanceList(props => {
  return (
    <Box
      gap='small'
    >
      {props.items.map(it => {
        return(
          <ItemRow item={{...it}} />
        )
      })}
    </Box>
  )
})

const AddFormEnchanced = GQLBuyNewItem(AddForm)

const Inventory = enchanceInventory((props) => (
  <Box>
    {
      props.isAdd &&
      <AddFormEnchanced
        done={() => props.setIsAdd(false)}
        close={() => props.setIsAdd(false)}
      />
    }
    <Box direction='row' pad={{ bottom: '40px' }}>
      <Box justify='center' pad={{ right: '20px' }}>
        <Text weight='bold'>Data Inventaris</Text>
      </Box>
      <Button label="Add" onClick={() => props.setIsAdd(true)}>
      </Button>
    </Box>
    <ItemList skip={props.skip} take={props.take}/>
  </Box>
))

export default Inventory