import React from 'react'
import { Box, Button, Layer, Text, Form, FormField } from 'grommet'
import createSelect from '../commons/SelectWithAvatar'
import { GQLListItem, GQLSellItem, getGlobalConfig } from '../../services'
import { compose, withStateHandlers } from 'recompose'

const enchance = compose(
  GQLSellItem,
  withStateHandlers(
    ({ idItem, ...rest }) => ({
      idItem,
      form: {
        item: {
          id: null,
          nama: '',
          avatar: null
        },
        nominal: 0,
        jumlah: 0,
        keterangan: ''
      },
      ...rest
    }),
    {
      updateForm: ({ form }) => (key, value) => {
        let obj = {}
        obj[key] = value
        return {
          form: Object.assign({}, form, obj)
        }
      },
      save: ({ mutate, form, idUser, done }) => () => {
        let { item, nominal, jumlah, keterangan } = form
        let idAddedBy = getGlobalConfig().ID_USER
        let payload = {
          nominal,
          jumlah,
          keterangan,
          idItem: item.id,
          idForUser: idUser,
          idAddedBy
        }
        mutate({
          variables: {
            payload
          }
        })
        done()
      }
    }
  )
)

const SelectItem = createSelect(GQLListItem, data => data.listItem)

const SellItem = ({ idUser, form, updateForm, save, done }) => 
  (<Layer modal onClickOutside={e => done()}>
    <Box pad='small' gap='small'>
      <Text weight='bold'>Input Data Penjualan</Text>

      <Form style={{
          maxHeight: '70vh',
          overflowY: 'scroll'
        }}
      >

        <SelectItem
          placeholder='Pilih Item'
          value={form.item}
          onChange={e => updateForm('item', e.option)}
          skip={0}
          take={100000}
        ></SelectItem>
        
        <FormField
          label="Jumlah"
          type="number"
          value={form.jumlah}
          onChange={e => {
            updateForm('jumlah', parseInt(e.target.value))
          }}
        >
        </FormField>

        <FormField
          label="Nominal"
          type="number"
          value={form.nominal}
          onChange={e => {
            updateForm('nominal', parseFloat(e.target.value))
          }}
        >
        </FormField>

        <FormField
          label="Keterangan"
          value={form.keterangan}
          onChange={e => {
            updateForm('keterangan', e.target.value)
          }}
        >
        </FormField>
        
      </Form>

      <Button 
        label="Save"
        onClick={e => {
          save()
        }}
      ></Button>
    </Box>
  </Layer>
)

export default enchance(SellItem)