import React from 'react'
import { Box } from 'grommet'
import { Star } from 'grommet-icons'
import { GQLRateSession } from '../../services'

const Rating = ({ mutate, id, pad, rating, size, refetchQueries }) => {
  let _pad = 'none'
  if (pad) {
    _pad = pad
  }
  let _size= 'medium'
  if (size) {
    _size = size
  }
  const stars = [1, 2, 3, 4, 5].map((it, index) => {
    let color = '#333'
    if (it <= rating) { color = 'gold' }
    return (
      <Star 
        color={color} 
        size={_size}
        onClick={() => {
          mutate({
            variables: {
              id,
              rating: (index + 1)
            },
            refetchQueries
          })
        }}
      />
    )
  })
  return (
    <Box 
      direction='row'
      justify='center'
      align='center'
      gap='xsmall'
      pad={_pad}
    >
      {stars}
    </Box>
  )
}

export default GQLRateSession(Rating)