import React, { Component } from 'react'
import { Box, DropButton, Grid, TextInput } from 'grommet'
import Tabs from '../commons/Tabs'
import { Route, withRouter } from 'react-router-dom'
import Barbermen from './Barbermen'
import User from './User'
import PaketJasa from './PaketJasa'
import AddCustomer from './AddCustomer'
import AddBarbermen from './AddBarbermen'
import AddPaketJasa from './AddPaketJasa'

const tabsOptions = {
  pad: {
    horizontal: 'small'
  },
  items: [
    'Customer',
    'Barbermen',
    'Jasa'
  ],
  gridArea: 'tab-box',
  direction: 'row',
  align: 'start',
  fill: true,
  style: {
    maxWidth: '400px'
  }
}

class MainContainer extends Component {

  state = {
    keyword: '',
    dialog: false
  }

  render () {
    return (
      <Box>

        <Grid
          gap='small'
          columns={['2/3', '1/3']}
          rows={['flex', 'flex', ['medium', 'full']]}
          areas={[
            { name: 'search', start: [0,1], end: [0,1] },
            { name: 'control', start: [1,0], end: [1,0] },
            { name: 'tab-box', start: [0,0], end: [0,0] },
            { name: 'content', start: [0,2], end: [1, 2] }
          ]}
        >
          <Box gridArea='search' fill>
            <TextInput
              plain 
              placeholder='Keyword'
              value={this.state.keyword}
              onChange={e => {
                this.setState({
                  keyword: e.target.value
                })
              }} />
          </Box>
          <Box gridArea='control' align='end' justify='center' fill>
            <DropButton color='neutral-3' label='Add'
              dropContent={
                <Box gap='small'>
                  <Box pad='small' 
                    onClick={() => this.setState({ dialog: 'add-customer' })}
                  >Customer</Box>
                  <Box
                    pad='small'
                    onClick={() => this.setState({ dialog: 'add-barbermen' })}
                  >Barbermen</Box>
                  <Box 
                    pad='small'
                    onClick={() => this.setState({ dialog: 'add-paket-jasa' })}
                  >PaketJasa</Box>
                </Box>
              }
            >
            </DropButton>
          </Box>

          <Tabs
            onChange={e => {
              if (e === 'Barbermen')
                this.props.history.push('/app/search/barbermen')
              if (e === 'Customer')
                this.props.history.push('/app/search/customer')
              if (e === 'Jasa')
                this.props.history.push('/app/search/jasa')
            }}
            {...tabsOptions}
          />

          <Box gridArea='content'>
            {
              (this.state.dialog === 'add-customer') && 
                <AddCustomer 
                  done={() => this.setState({ dialog: false })} 
                  cancel={() => this.setState({ dialog: false })} 
                />
            }
            {
              (this.state.dialog === 'add-barbermen') && 
                <AddBarbermen 
                  done={() => this.setState({ dialog: false })} 
                  cancel={() => this.setState({ dialog: false })} 
                />
            }
            {
              (this.state.dialog === 'add-paket-jasa') && 
                <AddPaketJasa 
                  done={() => this.setState({ dialog: false })} 
                  cancel={() => this.setState({ dialog: false })} 
                />
            }
            <Route 
              path="/app/search/barbermen" 
              render={props => (<Barbermen keyword={this.state.keyword} />)}
            />
            <Route 
              path="/app/search/customer" 
              render={props => (<User keyword={this.state.keyword} />)}
            />

            <Route 
              path="/app/search/jasa" 
              render={props => (<PaketJasa keyword={this.state.keyword} />)}
            />
          </Box>
        </Grid>

      </Box>
    )
  }
}

export default withRouter(MainContainer)