import React from 'react'
import { withRouter } from 'react-router-dom'
import { Grid, Box } from 'grommet'
import Tabs from '../commons/Tabs'
import { defaultStart, defaultEnd } from '../../services'
import FilterTime from '../commons/FilterTime'
import { 
  compose, 
  mapProps,
  withHandlers,
  withState
} from 'recompose'
import List from './List'

const gridOptions = {
  columns: ['1/2', 'auto'],
  rows: ['flex', 'flex', ['medium', 'full']],
  areas: [
    { name: 'tab-box', start: [0, 0], end: [0, 0] },
    { name: 'description', start: [0, 1], end: [0,1] },
    { name: 'content', start: [0, 2], end: [1, 2] }
  ],
  fill: true
}

const tabOptions = {
  gridArea: 'tab-box',
  items: [
    'All',
    'Open',
    'Ongoing',
    'Done',
    'Cancel'
  ],
  onChange: e => {
    console.log('change: ', e)
  }
}

const selectStates = (state) => {
  const STATES = ['SCHEDULED', 'DONE', 'ONGOING', 'CANCELED']
  switch (state) {
    case 'all':
      return STATES;
    case 'open':
      return STATES.filter(it => it === 'SCHEDULED')
    case 'ongoing':
      return STATES.filter(it => it === 'ONGOING')
    case 'done':
      return STATES.filter(it => it === 'DONE')
    case 'cancel':
      return STATES.filter(it => it === 'CANCELED')
    default:
      break
  }
}

const ALL = selectStates('all')

const handlers = withHandlers({
  onTabChange: props => e => {
    const target = e.toLowerCase()
    props.history.push(`/app/sesi/${target}`)
  }
})

const mapFilterStates = mapProps(props => {
  let { match, ...rest } = props
  let type = match.params.type
  let states = ALL 
  if (type !== '') {
    states = selectStates(type)
  }
  return {
    ...rest,
    states
  }
})

const enchance = compose(
  withRouter,
  handlers,
  mapFilterStates,
  withState('start', 'setStart', defaultStart()),
  withState('end', 'setEnd', defaultEnd())
)

const Sesi = enchance((props) => (
  <Grid {...gridOptions}>

    <Tabs 
      {...tabOptions}
      onChange={props.onTabChange}
    >
    </Tabs>

    <Box
      pad={{
        vertical: 'medium'
      }}
      gridArea='description'
    >
      <FilterTime
        start={props.start}
        setStart={props.setStart}
        end={props.end}
        setEnd={props.setEnd}
      />
    </Box>

    <Box
      gridArea='content'
      fill
    >
      <List states={props.states} start={props.start} end={props.end} />
    </Box>
  </Grid>
))

export default Sesi