import React, { Component } from 'react'
import { Box, Button, Layer, Form, FormField, RangeInput, Text } from 'grommet'
import { Down } from 'grommet-icons'
import { GQLMutationById, GQLUpdateMutation } from '../../services'
import Loading from '../commons/Loading'
import { compose, mapProps, withStateHandlers } from 'recompose'
import { withRouter } from 'react-router-dom'
import moment from 'moment'

const enchance = compose(
  GQLMutationById,
  Loading,
  mapProps( ({ data, ...rest }) => {
    return {
      item: data.mutationById,
      ...rest
    }
  }),
  withStateHandlers(
    ({ item, ...rest }) => {
      let form = {...item}
      let { waktu } = form
      form.tanggal = moment(waktu).format('YYYY-MM-DD')
      form.jam = moment(waktu).format('k')
      form.menit = moment(waktu).format('m')
      form.nominal = Math.abs(form.nominal)
      form.jumlah = Math.abs(form.jumlah)
      return {
        form,
        ...rest
      }
    },
    {
      updateForm: ({ form }) => (key, value) => {
        let current = { ...form }
        current[key] = value
        return {
          form: {...current}
        }
      }
    }
  )
)


const enchanceUpdate = compose(
  mapProps(({ form }) => {
    console.log(`${form.tanggal} ${form.jam}:${form.menit}`)
    let waktu = moment(`${form.tanggal} ${form.jam}:${form.menit}`, 'YYYY-MM-DD k:m')
    console.log('waktu : ', waktu)
    return {
      id: form.id,
      payload: {
        waktu,
        nominal: form.nominal,
        jumlah: form.jumlah,
        keterangan: form.keterangan
      }
    }
  }),
  GQLUpdateMutation
)

const UpdateMutation = enchanceUpdate(({ mutate }) => (
  <Button
    label="Ok"
    onClick={() => {
      mutate()
    }}
  ></Button>
))

class EditMutation extends Component {

  render () {
    let { close, mutate, form, updateForm } = this.props
    console.log('form: ', form)

    let boxContainerOptions = {
      width: '500px',
      pad: 'medium',
      gap: 'medium'
    }

    let layerOptions = {
      modal: true
    }

    return (
      <Layer {...layerOptions} onClickOutside={() => close()}>
        <Box {...boxContainerOptions}>
          <Text weight='bold'>Input Data Mutasi</Text>
          <Form>


            <FormField
              label="Nominal"
              value={form.nominal}
              type="number"
              min="0"
              onChange={e => updateForm('nominal', parseInt(e.target.value))}
            ></FormField>

            <FormField
              label="Jumlah"
              value={form.jumlah}
              type="number"
              min="0"
              onChange={e => updateForm('jumlah', parseInt(e.target.value))}
            ></FormField>

            <FormField
              label="Tanggal"
              type="date"
              value={form.tanggal}
              onChange={e => {
                updateForm('tanggal', e.target.value)
              }}
            ></FormField>

            <FormField
              label={`Jam: ${form.jam}`}
              htmlFor="jam-range"
            >
              <RangeInput
                id="jam-range"
                value={form.jam}
                min={0}
                max={23}
                onChange={e => {
                  updateForm('jam', e.target.value)
                }}
              />
            </FormField>

            <FormField
              label={`Menit: ${form.menit}`}
              htmlFor="menit-range"
            >
              <RangeInput
                id="menit-range"
                value={form.menit}
                min={0}
                max={59}
                onChange={e => {
                  updateForm('menit', e.target.value)
                }}
              />
            </FormField>

            <UpdateMutation form={form}/>

          </Form>
        </Box>
      </Layer>
    )
  }
}

export default enchance(EditMutation)