import React, { Component } from 'react'
import { Box, DropButton, Text } from 'grommet'
import { Down } from 'grommet-icons'
import { GQLCustomerById, GQLBarbermenById, GQLPaketJasaById } from '../../services'
import { compose, withState, mapProps } from 'recompose'
import moment from 'moment'
import Loading from '../commons/Loading'
import SesiRow from '../sesi/Row'

class DetailWithSesi extends Component {

  renderSesiList = () => {
    return (
      <Box gap='small'>
        {this.props.listSesi.map((it, index) => (<SesiRow key={`sesi-id${it.id}`} item={it} /> ))}
      </Box>
    )
  }

  renderDescription = () => {
    const imageSize = '150px'
    return (
      <Box
        direction='row'
        gap='medium'
        pad={{
          vertical: 'medium'
        }}
      >
        <Box
          style={{
            minWidth: imageSize,
            minHeight: imageSize
          }}
          background={`url(${this.props.item.avatar})`}
          border={{
            color: 'neutral-3',
            size: 'large'
          }}
        />

        <Box
          direction='column'
          justify='center'
        >
          {
            this.props.mainDescription(this.props.item)
          }
          {
            this.props.secondaryDescription(this.props.item)
          }
        </Box>
        
      </Box>
    )
  }
  
  render () {
    console.log(this.props)
    return (
      <Box
        pad='medium' gap='medium'>

        <Box pad='small' gap='small' direction='row' align='center'>
          <Text weight='bold' size='large'>
            Detail {this.props.typename}
          </Text>
        {
          this.props.actions &&
          this.props.actions(this.props.item.id)
        }
        </Box>

        {this.renderDescription()}

        <Box gap='small'>
          {this.renderSesiList()}
        </Box>
      </Box>
    )
  }
}

const CustomerDescription = (item) => {
  return (<Box
    direction='column'
    justify='start'
    gap='small'
  >
    <Text size='large' weight='bold'>{item.nama}</Text>
    <Text weight='bold'>{item.username}</Text>
    <Text>Total Kunjungan: {item.totalKunjungan}</Text>
    <Text>Kunjungan Terakhir: {item.kunjunganTerakhir}</Text>
  </Box>)
}

const CustomerAction = (id) => {
  return (
    <DropButton
      icon={<Down/>}
      dropContent={
        <Box pad='small' gap='small'>
          <Box>Jual Barang</Box>
          <Box>Hapus</Box>
        </Box>
      }
    >
    </DropButton>
  )
}

const BarbermenDescription = (item) => {
  return (<Box
    direction='column'
    justify='start'
    gap='small'
  >
    <Text size='large' weight='bold'>{item.nama}</Text>
    <Text>Total Sesi: {item.totalKunjungan}</Text>
    <Text>Sesi Terakhir: {item.kunjunganTerakhir}</Text>
  </Box>)
}

const PaketJasaDescription = (item) => {
  return (<Box
    direction='column'
    justify='start'
    gap='small'
  >
    <Text size='large' weight='bold'>{item.nama}</Text>
    <Text>Total Sesi: {item.totalKunjungan}</Text>
    <Text>Sesi Terakhir: {item.kunjunganTerakhir}</Text>
  </Box>)
}

const PaketJasaSecondaryDesc = (item) => {
  return (
    <Box
      direction='column'
      justify='start'
      gap='small'
    >
      <Text>Harga: {item.harga} IDR</Text>
      <Text>Estimasi Waktu: {item.estimasiWaktu}</Text>
      <Box direction='row'>
          Items: {item.items.map(it => <Text>{it}</Text>)}
      </Box>
    </Box>
  )
}

const DefaultSecondaryDescription = (item) => (<Box></Box>)

const withId = mapProps(({ match }) => {
  return {
    id: parseInt(match.params.id)
  }
})

const extractItem = (key) => mapProps(props => {
  let { sessions, __typename, ...item } = props.data[key]
  if (item.kunjunganTerakhir !== 'Never') {
    item.kunjunganTerakhir = moment(item.kunjunganTerakhir).calendar()
  }
  return {
    ...props,
    item,
    typename: __typename,
    listSesi: sessions
  }
})

const withDescription = ({ main, secondary, ...rest }) => mapProps(props => ({
  mainDescription: main,
  secondaryDescription: secondary,
  ...rest,
  ...props
}))

const buildDetail = ({ key, main, secondary, graphQuery, actions }) => compose(
  withId,
  withState('skip', 'setSkip', 0),
  withState('take', 'setTake', 30),
  graphQuery,
  Loading,
  extractItem(key),
  withDescription({ main, secondary, actions })
)(DetailWithSesi)

export const DetailCustomer = buildDetail({
  key: 'userById',
  main: CustomerDescription,
  secondary: DefaultSecondaryDescription,
  graphQuery: GQLCustomerById,
  actions: CustomerAction
})

export const DetailBarbermen = buildDetail({
  key: 'barbermenById',
  main: BarbermenDescription,
  secondary: DefaultSecondaryDescription,
  graphQuery: GQLBarbermenById
})

export const DetailPaketJasa = buildDetail({
  key: 'paketJasaById',
  main: PaketJasaDescription,
  secondary: PaketJasaSecondaryDesc,
  graphQuery: GQLPaketJasaById
})
