import React from 'react'
import { GQLFindUser } from '../../services'
import { compose, withHandlers, mapProps } from 'recompose'
import Loading from '../commons/Loading'
import UserRow from './UserRow'

const enchanceUserList = compose(
  GQLFindUser,
  Loading,
  mapProps(({ data: { findUser }, ...rest }) => {
    return {
      items: findUser,
      ...rest
    }
  }),
  withHandlers({
    onOpenSession: props => id => {
      props.onOpenSession(id)
    },
    onDetailSession: props => id => {
      props.onDetailSession(id)
    },
    onDeleteSession: props => event => {
      console.log('DELETE USER')
    }
  })
)

const UserList = enchanceUserList((props) => (
  props.items.map((it, index) => 
    <UserRow 
      key={`user-row#${index}`}
      onOpenSession={props.onOpenSession}
      onDetailSession={props.onDetailSession}
      onDeleteSession={props.onDeleteSession}
      {...it}
    />
  )
))

export default UserList