import React from 'react'
import { Box } from 'grommet'
import { compose, mapProps } from 'recompose'
import Row from './Row'
import Loading from '../commons/Loading'
import { GQLListSesi } from '../../services'

const enchance = compose(
  mapProps(props => {
    return {
      start: props.start,
      end: props.end,
      states: props.states
    }
  }),
  GQLListSesi,
  Loading,
)

export default enchance((props) => {
  const { data } = props
  return  (
    <Box gap='medium'>
      {data.listSessionInRange.map((it, index) => (<Row key={`sesi-id${it.id}`} item={it} /> ))}
    </Box>
  )
})