import React, { Component } from 'react'
import { 
  Box, 
  Button, 
  DropButton, 
  Form, 
  FormField, 
  Layer, 
  Select, 
  Text } from 'grommet'
import { Down } from 'grommet-icons'
import { GQLBarbermenById, GQLChangeBarbermenName } from '../../services'
import { compose, withState, mapProps } from 'recompose'
import moment from 'moment'
import Loading from '../commons/Loading'
import SesiRow from '../sesi/Row'

const withId = mapProps(({ match }) => {
  return {
    id: parseInt(match.params.id)
  }
})

const extractItem = mapProps(props => {
  let { sessions, __typename, ...item } = props.data.barbermenById
  if (item.kunjunganTerakhir !== 'Never') {
    item.kunjunganTerakhir = moment(item.kunjunganTerakhir).calendar()
  }
  return {
    ...props,
    item,
    typename: __typename,
    listSesi: sessions
  }
})

const enchance = compose(
  withId,
  withState('skip', 'setSkip', 0),
  withState('take', 'setTake', 30),
  GQLBarbermenById,
  Loading,
  extractItem
)

class FormEditNama extends Component {

  state = {
    nama: ''
  }

  render () {
    return (
      <Layer modal onClickOutside={e => { this.props.close() }}>
        <Box
          style={{
            minWidth: '500px'
          }}
          pad='medium'
          gap='medium'
        >
          <Text size='medium' weight='bold'>Input Nama Barbermen</Text>

          <Form>
            <FormField
              label="Nama"
              value={this.state.nama}
              onChange={e => {
                this.setState({
                  nama: e.target.value
                })
              }}
            >
            </FormField>

            <Button
              label="Ok"
              onClick={e => {
                this.props.mutate({
                  variables: {
                    id: this.props.id,
                    nama: this.state.nama
                  }
                })
                this.props.close()
              }}
            ></Button>
          </Form>
        </Box>
      </Layer>
    )
  }
}

const GQLFormEditNama = GQLChangeBarbermenName(FormEditNama)

class DetailBarbermen extends Component {

  state = {
    mode: 'read'
  }

  renderSesiList = () => {
    return (
      <Box gap='small'>
        {this.props.listSesi.map((it, index) => (<SesiRow key={`sesi-id${it.id}`} item={it} /> ))}
      </Box>
    )
  }

  renderDescription = () => {
    const imageSize = '150px'
    return (
      <Box
        direction='row'
        gap='medium'
      >
        <Box
          style={{
            minWidth: imageSize,
            minHeight: imageSize
          }}
          background={`url(${this.props.item.avatar})`}
          border={{
            color: 'neutral-3',
            size: 'large'
          }}
        />

        <Box
          direction='column'
          justify='center'
        >
          <Box
            direction='column'
            justify='center'
          >
            <Box
              direction='column'
              justify='start'
              gap='small'
            >
              <Text size='large' weight='bold'>{this.props.item.nama}</Text>
              <Text>Total Kunjungan: {this.props.item.totalKunjungan}</Text>
              <Text>Kunjungan Terakhir: {this.props.item.kunjunganTerakhir}</Text>
            </Box>
          </Box>
        </Box>
        
      </Box>
    )
  }
  
  render () {
    return (
      <Box gap='medium'>

        {
          (this.state.mode === 'edit-nama')
          && (<GQLFormEditNama id={this.props.id} close={() => { this.setState({ mode: 'read' }) }} />)
        }

        <Box gap='small' direction='row' align='center'>
          <Text weight='bold' size='large'>
            Detail {this.props.typename}
          </Text>
          <DropButton
            icon={<Down/>}
            dropContent={
              <Box pad='small' gap='small'>
                <Box
                  onClick={e => {
                    this.setState({
                      mode: 'edit-nama'
                    })
                  }}
                >Ganti Nama</Box>
                <Box>Hapus</Box>
              </Box>
            }
          >
          </DropButton>
        </Box>

        {this.renderDescription()}

        <Box gap='small'>
          <Box direction='row' align='center' gap='medium'>
            <Text>Jumlah data sesi yang ditampilkan</Text>
            <Select
              options={[30, 50, 100]}
              value={this.props.take}
              onChange={({ option }) => {
                this.props.setTake(option)
              }}
              style={{
                maxWidth: '300px'
              }}
            >
            </Select>
          </Box>
          {this.renderSesiList()}
        </Box>
      </Box>
    )
  }
}

export default enchance(DetailBarbermen)