import React, { Fragment } from 'react'
import { Route } from 'react-router-dom'
import { DetailPaketJasa } from './DetailWithSesi'
import DetailCustomer from './DetailCustomer'
import DetailBarbermen from './DetailBarbermen'
import DetailItem from './DetailItem'
import DetailSesi from './DetailSesi'
import DetailMutation from './DetailMutation'

export default (props) => (
  <Fragment>
    <Route 
      path="/app/detail/customer/:id" 
      component={DetailCustomer}
    />
    <Route 
      path="/app/detail/barbermen/:id" 
      component={DetailBarbermen}
    />
    <Route 
      path="/app/detail/paketjasa/:id" 
      component={DetailPaketJasa}
    />
    <Route 
      path="/app/detail/item/:id"
      component={DetailItem}
    />
    <Route 
      path="/app/detail/mutation/:id"
      component={DetailMutation}
    />
    <Route 
      path="/app/detail/sesi/:id"
      component={DetailSesi}
    />
  </Fragment>
)
