export const GQLListSesi = graphql(
  gql`
    query GetSessions ($options: FindSessionInRangeOptions!) {
      listSessionInRange(options: $options) {
        ...SesiData
      }
    }
    ${AppFragments.sesi}
  `,
  {
    options: (props) => {
      let { states, start, end } = props
      const { ID_CABANG } = getGlobalConfig()
      if (!start) {
        start = defaultStart()
      }
      if (!end) {
        end = defaultEnd()
      }
      return {
        variables: {
          options: {
            idCabang: ID_CABANG,
            start: start.toISOString(),
            end: end.toISOString(),
            states
          }
        }
      }
    }
  }
)

