export const GQLUpdateItem = graphql(
  gql`
    mutation UpdateItem ($id: Int!, $payload: ItemUpdateInput!) {
      updateItem (id: $id, payload: $payload) {
        ...ItemData
      }
    }
    ${AppFragments.item}
  `,
  {
    options: ({ id, item }) => {
      let payload = Object.assign({}, item)
      return {
        variables: {
          id,
          payload
        }
      }
    }
  }
)

export const GQLUseItem = graphql(
  gql`
    mutation UseItem($payload: BaseMutasiInput!) {
      useItem (payload: $payload) {
        __typename
        id
        jumlah
        nominal
        waktu
        keterangan
      }
    }
  `,
  {
    options: props => {
      let form = { ...props.form }
      form.nominal = 0
      return {
        variables: {
          payload: {
            idAddedBy: getGlobalConfig().ID_USER,
            idItem: props.idItem,
            ...form
          }
        },
        refetchQueries: [
          'ItemById'
        ]
      }
    }
  }
)

