import React from 'react'
import { Box, Button, Form, FormField, Layer, Text } from 'grommet'
import { compose, withStateHandlers } from 'recompose'
import { GQLBuyMoreItem } from '../../services'
import Loading from '../commons/Loading'

const enchance = compose(
  withStateHandlers(
    ({ idItem, ...rest }) => {
      return {
        idItem,
        form: {
          jumlah: 0,
          nominal: 0,
          keterangan: ''
        },
        ...rest
      }
    },
    {
      updateForm: ({ form }) => (key, value) => {
        let curr = {...form}
        curr[key] = value
        return {
          form: curr
        }
      }
    }
  ),
  GQLBuyMoreItem,
  Loading
)

const BuyMoreItem = ({ mutate, form, updateForm, close }) => {
  return (
    <Layer modal onClickOutside={e => close()}>
      <Box pad='medium' gap='medium' width='600px'>
        <Text>Input Data Pembelian</Text>

        <Form style={{
          maxHeight: '60vh',
          overflowY: 'scroll'
        }}>

          <FormField
            label="Jumlah"
            type="number"
            value={form.jumlah}
            onChange={e => {
              updateForm('jumlah', parseInt(e.target.value))
            }}
          >
          </FormField>

          <FormField
            label="Nominal"
            type="number"
            value={form.nominal}
            onChange={e => {
              updateForm('nominal', parseFloat(e.target.value))
            }}
          >
          </FormField>

          <FormField
            label="Keterangan"
            value={form.keterangan}
            onChange={e => {
              updateForm('keterangan', e.target.value)
            }}
          >
          </FormField>

        </Form>

        <Button
          label="Save"
          onClick={event => {
            mutate()
            close()
          }}
        ></Button>
      </Box>
    </Layer>
  )
}

export default enchance(BuyMoreItem)