import React, { Fragment } from 'react'
import { Layer, Box, Text }  from 'grommet'
import { withRouter } from 'react-router-dom';
import { 
  Currency, 
  Clock, 
  Search, 
  PowerShutdown, 
  Briefcase, 
  Book, 
  Brush,
  Cut,
  Cubes,
  Emoji,
  Sync
} from 'grommet-icons'
import { 
  GQLNextSession,
  GQLLastSession,
  GQLCountPerState
} from '../../services'
import Loading from '../commons/Loading'
import { 
  compose,
  mapProps,
  withHandlers
} from 'recompose'
import moment from 'moment'

const StateMenu = ({ nama, value, color, icon, ...rest }) => {
  let _color = color ? color : 'neutral-2'
  return (
    <Box direction='row' 
      gap='small'
      align='center'
      {...rest}
    >
      <Box basis='38px'>
        {icon}
      </Box>
      <Box basis='1/2'>
        <Text weight='bold' size='small'>{nama}: </Text>
      </Box>
      <Box fill={false} pad='xsmall'>
        <Text size='small' weight='bold'>{value}</Text>
      </Box>
    </Box>
  )
}

const enchanceNextSession = compose(
  // Desired HOC.
  GQLNextSession,
  // Show loading indicator when network status is not 7 (Ready)
  Loading,
  // Navigate to desired target when clicked
  mapProps(({ data: { nextSession } }) => {
    return {
      label: 'Next Session',
      time: nextSession.scheduledStartTime,
      id: nextSession.id
    }
  }),
  withRouter
)

const enchanceLastSession = compose(
  // Desired HOC.
  GQLLastSession,
  // Show loading indicator when network status is not 7 (Ready)
  Loading,
  // Navigate to desired target when clicked
  mapProps(({ data: { lastSession } }) => {
    return {
      label: 'Last Session',
      time: lastSession.executionEndTime,
      id: lastSession.id
    }
  }),
  withRouter
)

const enchanceCountPerState = compose(
  GQLCountPerState,
  Loading,
  withRouter,
  mapProps(({ data: { countPerState }, history }) => {
    return {
      history,
      ...countPerState
    }
  }),
  withHandlers({
    navigate: props => path => {
      props.history.push(path)
    }
  })
)

const SesiSummary = enchanceCountPerState( props => (
  <Box pad={{ vertical: 'medium' }} gap='small'>
    <StateMenu onClick={() => props.navigate('/app/sesi/open')} icon={<Book/>} nama='Open' value={props.SCHEDULED} color='neutral-3'/>
    <StateMenu onClick={() => props.navigate('/app/sesi/done')} icon={<Brush/>} nama='Done' value={props.DONE} color='neutral-4'/>
    <StateMenu onClick={() => props.navigate('/app/sesi/ongoing')} icon={<Clock/>} nama='Ongoing' value={props.ONGOING}/>
    <StateMenu onClick={() => props.navigate('/app/sesi/canceled')} icon={<PowerShutdown/>} nama='Cancel' value={props.CANCELED}/>
  </Box>  
) )

const NextOrLast = ({ label, time, history, id }) => (
  <Fragment>
    <Text size='medium'>{label}</Text>
    <Box direction='row' 
      gap='small'
      align='center'
      onClick={() => {
        history.push('/app/detail/sesi/' + id)
      }}
    >
      <Box basis='38px'>
        <Sync/>
      </Box>
      <Box fill={false} pad='xsmall'>
        <Text size='small' weight='bold'>{moment(time).calendar()}</Text>
      </Box>
    </Box>
  </Fragment>
)

export const NextSession = enchanceNextSession(NextOrLast)
export const LastSession = enchanceLastSession(NextOrLast)

const menus = [
  {
    name: 'Transaksi',
    icon: Currency,
    to: '/app/transaksi'
  },
  {
    name: 'Sesi',
    icon: Clock,
    to: '/app/sesi/all'
  },
  {
    name: 'Search',
    icon: Search,
    to: '/app/search/customer'
  },
  {
    name: 'Inventaris',
    icon: Briefcase,
    to: '/app/inventory'
  }
];

const MenuBoxItem = withRouter((props) => {
  return (
    <Box 
      direction='row' fill='horizontal' 
      gap='medium' align='center'
      onClick={() => props.history.push(`${props.to}`)}
    >
      {<props.icon color='white' size='medium' />}
      <Text margin='none'>
        {props.name}
      </Text>
    </Box>
  )
})

export default function Sidebar (props) {
  return (
    <Layer
      full='vertical'
      modal={false}
      position='left'
      style={{
        width: `${props.width}px`,
        top: '48px',
        zIndex: 10
      }}
    >
      <Box
        fill
        background='light-1'
        pad={{
          vertical: 'medium',
          horizontal: 'medium'
        }}
        gap='small'
        style={{
          overflowY: 'scroll'
        }}
      >
        <NextSession/>
        <LastSession/>

        <SesiSummary/>

        <Text size='medium'>Recent</Text>
        <StateMenu icon={<Cut/>} nama='Barbermen' value='8'/>
        <StateMenu icon={<Cubes/>} nama='Paket Jasa' value='8'/>
        <StateMenu icon={<Emoji/>} nama='Customer' value='8'/>
      </Box>
    </Layer>
  )
}